.horizontal-timeline-container {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 250px;
  background-color: rgba(14, 20, 17, 0.85);
  backdrop-filter: blur(5px);
  overflow: hidden;
  color: #e0e0e0;
  border-top: 2px solid #333;
  z-index: 100;
  transition: all 0.4s ease-in-out;
}

.horizontal-timeline-container.state-collapsed {
  height: 100px;
  cursor: default;
}

.horizontal-timeline-container.state-expanded {
  height: 60vh;
  bottom: 20vh; /* Detaches the timeline from the bottom of the viewport */
  border-radius: 12px 12px 0 0; /* Rounds only the top corners */
  box-shadow: 0 -4px 25px rgba(0, 0, 0, 0.3);
}

.horizontal-timeline-wrapper {
  position: absolute;
  top: 50px; /* Give space for buttons at the top */
  left: 0;
  z-index: 1; /* Ensure wrapper is behind UI controls */
  height: calc(100% - 40px);
  display: flex;
  align-items: flex-start;
  padding: 20px;
  box-sizing: border-box;
  padding-top: 0;
  cursor: grab;
  transform: translateY(0);
}

.horizontal-timeline-wrapper:active {
  cursor: grabbing;
}

.horizontal-timeline-container.state-collapsed .horizontal-timeline-wrapper {
  transform: translateY(50px); /* Move it down to hide it */
  pointer-events: none;
}

.time-ruler {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 20px;
  border-bottom: 1px solid #555;
}

.time-marker {
  position: absolute;
  height: 10px;
  top: 0;
  border-left: 1px solid #555;
}

.time-marker-label {
  position: absolute;
  top: -20px; /* Adjust to be above the new ruler position */
  transform: translateX(-50%);
  font-size: 10px;
  color: #aaa;
  white-space: nowrap;
}

.era-block {
  position: absolute;
  top: 20px; /* Position below the time ruler */
  height: calc(100% - 20px);
  border-left: 1px solid rgba(255, 255, 255, 0.7);
  padding: 10px;
  box-sizing: border-box;
}

.era-block.paleozoic { background-color: rgba(87, 125, 134, 0.3); }
.era-block.mesozoic { background-color: rgba(134, 123, 87, 0.3); }
.era-block.cenozoic { background-color: rgba(87, 134, 93, 0.3); }

.era-title {
  margin: 0;
  padding: 0 5px;
  font-size: 1.5em;
  font-weight: bold;
  white-space: nowrap;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
  border-radius: 4px;
  display: inline-block;
}

.era-title:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.era-title.active {
  background-color: #ffae42;
  color: #1a1a1a;
}

.periods-container {
  position: relative;
  width: 100%;
  height: calc(100% - 40px);
  margin-top: 10px;
}

.period-block {
  position: absolute;
  height: 100%;
  border-left: 1px solid rgba(255, 255, 255, 0.4);
  padding: 5px;
  box-sizing: border-box;
}

.period-block .fauna-container {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* Container shouldn't block clicks on things behind it */
}

.period-title {
  margin: 0;
  padding: 0;
  font-size: 1.1em;
  white-space: nowrap;
  color: #ddd;
}

.epochs-container {
    position: relative;
    width: 100%;
    height: calc(100% - 30px);
    margin-top: 5px;
}

.epoch-block {
    position: absolute;
    height: 100%;
    border-left: 1px solid rgba(255, 255, 255, 0.2);
    padding: 2px 5px;
    box-sizing: border-box;
}

.epoch-title {
    margin: 0;
    padding: 0;
    font-size: 0.8em;
    white-space: nowrap;
    color: #bbb;
    writing-mode: vertical-rl;
    transform: rotate(180deg);
}

.fauna-marker {
  position: absolute;
  /* bottom is now set inline for staggering */
  height: 20px;
  background-color: #ffae42;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: auto; /* Markers should be clickable */
  cursor: pointer;
  transition: transform 0.2s ease-out, background-color 0.2s;
  box-shadow: 0 2px 5px rgba(0,0,0,0.4);
}

.fauna-marker:hover {
  transform: scale(1.1);
  background-color: #ffc97f;
  z-index: 11;
}

.fauna-label {
  color: #1a1a1a;
  font-size: 12px;
  font-weight: bold;
  padding: 0 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.timeline-toggle-button {
  position: absolute;
  top: 10px;
  right: 20px;
  z-index: 101; /* Above other timeline content */
  background-color: rgba(51, 51, 51, 0.5);
  color: #fff;
  border: 1px solid #555;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.timeline-toggle-button svg {
  width: 40px;
  height: 40px;
}

.timeline-resize-handle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 8px;
  cursor: ns-resize;
  z-index: 102;
}

.horizontal-timeline-container.state-collapsed .timeline-toggle-button {
  transform: rotate(180deg);
}

.timeline-expand-button {
  position: absolute;
  top: 10px;
  right: 94px; /* 20px (from right) + 64px (toggle button) + 10px (gap) */
  z-index: 101;
  background-color: rgba(51, 51, 51, 0.5);
  color: #fff;
  border: 1px solid #555;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: opacity 0.4s ease-out, transform 0.4s cubic-bezier(0.25, 1, 0.5, 1), background-color 0.2s;
  transform-origin: 150% 50%; /* Animate from a point to the right of the button */
  transform: scale(1);
}

.timeline-expand-button svg {
  width: 40px;
  height: 40px;
}

.horizontal-timeline-container.state-collapsed .timeline-expand-button {
  opacity: 0;
  pointer-events: none;
  transform: translateX(74px) scale(0.6);
}

.timeline-clear-filter-button {
  position: absolute;
  top: 21px; /* Vertically align with other buttons */
  right: 168px; /* 94px (from right) + 64px (expand button) + 10px (gap) */
  z-index: 101;
  background-color: #ffae42;
  color: #1a1a1a;
  border: none;
  border-radius: 8px;
  padding: 12px 24px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  transition: opacity 0.4s ease-out, transform 0.4s cubic-bezier(0.25, 1, 0.5, 1), background-color 0.2s;
  transform-origin: right center;
  opacity: 0;
  pointer-events: none;
  transform: translateX(50px) scale(0.6);
}

.horizontal-timeline-container:not(.state-collapsed).filter-active .timeline-clear-filter-button {
  opacity: 1;
  pointer-events: auto;
  transform: translateX(0) scale(1);
}

.timeline-clear-filter-button:hover {
  background-color: #ffc97f;
}
